// retire-lah-client/src/App.js
import React, { useState, useEffect, useCallback } from 'react';
import InputForm from './components/InputForm';
import ProjectionDisplay from './components/ProjectionDisplay';
import Footer from './components/Footer';
import { calculateMonthlySavingsNeeded, calculateTimeToFIRE } from './utils/calculator';
import ReactGA from 'react-ga4';

const CALC_MODES = { MONTHLY_SAVINGS: 'calculateMonthlySavings', TIME_TO_FIRE: 'calculateTimeToFIRE' };
const LOCAL_STORAGE_KEY = 'retireLahScenarios_v1';

function App() {
  // ... (calcMode, initialFormState, formData, projectionData, isCalculating, isInitialLoading, error states remain the same) ...
  const initialFormState = { currentAge: 30, retirementAge: 55, desiredMonthlyLifestyleToday: 4000, assumedInflationRate: 2.5, initialInvestment: 0, instrument: 'spy', plannedMonthlyInvestment: 1500, safeWithdrawalRate: 4,};
  const [calcMode, setCalcMode] = React.useState(CALC_MODES.MONTHLY_SAVINGS);
  const [formData, setFormData] = React.useState(() => { const lastFormData = localStorage.getItem('retireLahLastForm'); return lastFormData ? { ...initialFormState, ...JSON.parse(lastFormData) } : initialFormState;});
  const [projectionData, setProjectionData] = React.useState(null);
  const [isCalculating, setIsCalculating] = React.useState(false);
  const [isInitialLoading, setIsInitialLoading] = React.useState(true);
  const [error, setError] = React.useState(null);


  const [savedScenarios, setSavedScenarios] = useState([]);
  const [scenarioName, setScenarioName] = useState("");
  const [isScenarioManagerOpen, setIsScenarioManagerOpen] = useState(false); // New state for collapsible section

  // ... (useEffect for loading/saving scenarios, performCalculation, useEffect for initial load, handleSubmit, handleModeChange, handleSaveScenario, handleLoadScenario, handleDeleteScenario remain the same as the last full App.js script) ...
  React.useEffect(() => { try { const storedScenarios = localStorage.getItem(LOCAL_STORAGE_KEY); if (storedScenarios) { setSavedScenarios(JSON.parse(storedScenarios)); } } catch (e) { console.error("Error loading scenarios:", e); localStorage.removeItem(LOCAL_STORAGE_KEY); } }, []);
  React.useEffect(() => { try { localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(savedScenarios)); } catch (e) { console.error("Error saving scenarios:", e); } }, [savedScenarios]);
  React.useEffect(() => { try { localStorage.setItem('retireLahLastForm', JSON.stringify(formData)); } catch(e) { console.error("Error saving last form:", e); } }, [formData]);
  const performCalculation = React.useCallback((dataToCalculate, mode) => { setError(null); try { let result; if (mode === CALC_MODES.MONTHLY_SAVINGS) { result = calculateMonthlySavingsNeeded(dataToCalculate); } else if (mode === CALC_MODES.TIME_TO_FIRE) { result = calculateTimeToFIRE(dataToCalculate); } else { throw new Error("Invalid calculation mode."); } if (result) { setProjectionData(result); if (ReactGA.isInitialized) ReactGA.event({ category: "Retirement_Calculator", action: "Calculate_Projection_Success", label: `Mode: ${mode}` }); } else { setProjectionData(null); } } catch (err) { setError(err.message || "An error occurred."); setProjectionData(null); if (ReactGA.isInitialized) ReactGA.event({ category: "Retirement_Calculator", action: "Calculate_Projection_Error", label: `Mode: ${mode} | Error: ${err.message || "Unknown"}`}); } }, []);
  React.useEffect(() => { setIsCalculating(true); setIsInitialLoading(true); const timer = setTimeout(() => { performCalculation(formData, calcMode); setIsCalculating(false); setIsInitialLoading(false); }, 100); return () => clearTimeout(timer); }, [performCalculation, calcMode]);
  const handleSubmit = (e) => { if (e) e.preventDefault(); setIsCalculating(true); const timer = setTimeout(() => { performCalculation(formData, calcMode); setIsCalculating(false); }, 150);};
  const handleModeChange = (newMode) => { setCalcMode(newMode); setProjectionData(null); setError(null); if (ReactGA.isInitialized) ReactGA.event({ category: "Retirement_Calculator", action: "Switch_Mode", label: `Switched to: ${newMode}` }); setIsCalculating(true); setTimeout(() => { performCalculation(formData, newMode); setIsCalculating(false); }, 50);};
  const handleSaveScenario = () => { if (!scenarioName.trim()) { alert("Please enter a name for your scenario."); return; } if (savedScenarios.find(s => s.name.toLowerCase() === scenarioName.trim().toLowerCase())) { alert("A scenario with this name already exists."); return; } const newScenario = { name: scenarioName.trim(), id: Date.now(), formData: { ...formData }, calcMode: calcMode }; setSavedScenarios(prevScenarios => [...prevScenarios, newScenario].sort((a,b) => a.name.localeCompare(b.name))); setScenarioName(""); alert("Scenario saved!"); if (ReactGA.isInitialized) ReactGA.event({ category: "Scenario_Management", action: "Save_Scenario"});};
  const handleLoadScenario = (scenarioId) => { const scenarioToLoad = savedScenarios.find(s => s.id === scenarioId); if (scenarioToLoad) { setFormData(scenarioToLoad.formData); setCalcMode(scenarioToLoad.calcMode); setIsCalculating(true); setIsInitialLoading(false); setTimeout(() => { performCalculation(scenarioToLoad.formData, scenarioToLoad.calcMode); setIsCalculating(false); }, 100); if (ReactGA.isInitialized) ReactGA.event({ category: "Scenario_Management", action: "Load_Scenario"});}};
  const handleDeleteScenario = (scenarioIdToDelete) => { if (window.confirm("Are you sure you want to delete this scenario?")) { setSavedScenarios(prevScenarios => prevScenarios.filter(s => s.id !== scenarioIdToDelete)); if (ReactGA.isInitialized) ReactGA.event({ category: "Scenario_Management", action: "Delete_Scenario"});}};


  const HeaderIcon = () => ( /* ... icon ... */ <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 sm:h-9 md:h-10 mr-3 text-white opacity-90" fill="none" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="1.5"><path strokeLinecap="round" strokeLinejoin="round" d="M17 9V7a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2m2 4h10a2 2 0 002-2v-6a2 2 0 00-2-2H9a2 2 0 00-2 2v6a2 2 0 002 2zm7-5a2 2 0 11-4 0 2 2 0 014 0z" /></svg>);
  
  return (
    <div className="min-h-screen flex flex-col bg-brand-light-bg">
      <header> {/* ... Header content ... */} <div className="bg-brand-primary text-white py-4 sm:py-5 shadow-lg sticky top-0 z-50"><div className="container mx-auto px-4 text-center"><h1 className="text-2xl sm:text-3xl md:text-4xl font-bold flex items-center justify-center tracking-tight"><HeaderIcon />Retire Lah!</h1><p className="text-xs sm:text-sm opacity-90 mt-1">Your F.I.R.E. (Financial Independence, Retire Early) planner for Singaporeans.</p></div></div></header>

      <main className="container mx-auto px-3 sm:px-4 py-6 sm:py-8 flex-grow w-full max-w-4xl xl:max-w-5xl self-center">
        {/* Mode Toggle Buttons */}
        <div className="mb-6 flex flex-col sm:flex-row justify-center space-y-2 sm:space-y-0 sm:space-x-2 md:space-x-4"> <button onClick={() => handleModeChange(CALC_MODES.MONTHLY_SAVINGS)} className={`w-full sm:w-auto px-4 py-2.5 rounded-lg text-sm font-semibold transition-all duration-200 ease-in-out focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-brand-primary ${calcMode === CALC_MODES.MONTHLY_SAVINGS ? 'bg-brand-primary text-white shadow-md' : 'bg-white text-brand-primary border border-brand-primary hover:bg-blue-50'}`}>Calc Monthly Savings</button><button onClick={() => handleModeChange(CALC_MODES.TIME_TO_FIRE)} className={`w-full sm:w-auto px-4 py-2.5 rounded-lg text-sm font-semibold transition-all duration-200 ease-in-out focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-brand-secondary ${calcMode === CALC_MODES.TIME_TO_FIRE ? 'bg-brand-secondary text-white shadow-md' : 'bg-white text-brand-secondary border border-brand-secondary hover:bg-green-50'}`}>Calc Time to F.I.R.E.</button></div>
        
        <InputForm formData={formData} setFormData={setFormData} onSubmit={handleSubmit} isLoading={isCalculating} calcMode={calcMode} CALC_MODES={CALC_MODES} />
        
        {/* Scenario Management Section - Now Collapsible */}
        <div className="mt-8 bg-white rounded-xl shadow-card-lg">
          <button
            onClick={() => setIsScenarioManagerOpen(!isScenarioManagerOpen)}
            className="w-full flex justify-between items-center p-4 sm:p-6 text-left focus:outline-none focus-visible:ring-2 focus-visible:ring-brand-primary focus-visible:ring-opacity-75 rounded-t-xl hover:bg-gray-50 transition-colors"
            aria-expanded={isScenarioManagerOpen}
            aria-controls="scenario-details"
          >
            <h3 className="text-md sm:text-lg font-semibold text-brand-dark-text">
              {isScenarioManagerOpen ? 'Hide Scenario Manager' : 'Show Scenario Manager'}
            </h3>
            <svg 
              className={`w-5 h-5 sm:w-6 sm:h-6 text-brand-primary transform transition-transform duration-200 ${isScenarioManagerOpen ? 'rotate-180' : 'rotate-0'}`} 
              xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor"
            >
              <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd" />
            </svg>
          </button>
          {/* Collapsible Content */}
          {isScenarioManagerOpen && (
            <div id="scenario-details" className="p-4 sm:p-6 border-t border-gray-200">
              <div className="flex flex-col sm:flex-row gap-3 mb-4 items-stretch sm:items-end">
                <div className="flex-grow">
                    <label htmlFor="scenarioName" className="block text-xs font-medium text-brand-medium-text mb-1">Scenario Name</label>
                    <input 
                      type="text" id="scenarioName" value={scenarioName} 
                      onChange={(e) => setScenarioName(e.target.value)}
                      placeholder="e.g., My FIRE Plan"
                      className="w-full py-2 px-3 border border-gray-300 rounded-md shadow-sm text-sm"
                    />
                </div>
                <button 
                  onClick={handleSaveScenario}
                  className="w-full sm:w-auto bg-accent-orange hover:bg-amber-600 text-white font-semibold py-2 px-4 rounded-md shadow-sm text-sm transition-colors h-9 sm:h-auto whitespace-nowrap"
                >
                  Save Current
                </button>
              </div>
              {savedScenarios.length > 0 && (
                <div>
                  <h4 className="text-md font-medium text-brand-medium-text mb-2 mt-4">Saved Scenarios:</h4>
                  <ul className="space-y-2 max-h-60 overflow-y-auto border rounded-md p-2 sm:p-3 bg-gray-50">
                    {savedScenarios.map((scenario) => (
                      <li key={scenario.id} className="flex flex-col space-y-2 sm:space-y-0 sm:flex-row sm:justify-between sm:items-center p-2 sm:p-2.5 bg-white rounded shadow-sm hover:bg-gray-100 transition-colors">
                        <span className="text-sm text-brand-dark-text font-medium truncate pr-2 w-full sm:w-auto" title={scenario.name}>{scenario.name}</span>
                        <div className="flex-shrink-0 space-x-2 w-full sm:w-auto flex justify-end">
                          <button onClick={() => handleLoadScenario(scenario.id)} className="text-xs bg-blue-500 hover:bg-blue-600 text-white font-semibold py-1 px-3 rounded transition-colors">Load</button>
                          <button onClick={() => handleDeleteScenario(scenario.id)} className="text-xs bg-red-500 hover:bg-red-600 text-white font-semibold py-1 px-3 rounded transition-colors">Del</button>
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
              {savedScenarios.length === 0 && <p className="text-sm text-brand-light-text text-center py-4">No scenarios saved yet.</p>}
            </div>
          )}
        </div>

        {/* Error, Initial Loader, Projection Display sections remain the same */}
        {error && ( <div className="mt-8 bg-red-50 border-l-4 border-red-500 text-red-700 p-4 rounded-md shadow-md" role="alert"><div className="flex"><div className="py-1"><svg className="fill-current h-6 w-6 text-red-500 mr-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M2.93 17.07A10 10 0 1 1 17.07 2.93 10 10 0 0 1 2.93 17.07zM9 5v6h2V5H9zm0 8v2h2v-2H9z"/></svg></div><div><p className="font-bold text-red-800">Calculation Error</p><p className="text-sm text-red-700">{error}</p></div></div></div>)}
        {isInitialLoading && !error && ( <div className="mt-12 text-center flex flex-col items-center justify-center py-16"><div className="animate-spin rounded-full h-12 w-12 sm:h-16 sm:w-16 border-t-4 border-b-4 border-brand-primary mb-6"></div><p className="text-brand-medium-text text-lg sm:text-xl font-medium">Loading Initial Projection...</p></div>)}
        {!isInitialLoading && projectionData && !error && ( <ProjectionDisplay data={projectionData} calcMode={calcMode} CALC_MODES={CALC_MODES}/> )}
        {!isInitialLoading && !projectionData && !error && ( <div className="mt-12 text-center flex flex-col items-center justify-center py-16"><p className="text-brand-medium-text text-lg">Please adjust your plan and calculate, or switch modes.</p></div> )}
      </main>
      <Footer />
    </div>
  );
}
export default App;