// retire-lah-client/src/utils/calculator.js

export const INSTRUMENT_DETAILS = {
    spy: { name: "SPY (S&P 500)", grossReturn: 0.10, expenseRatio: 0.000945 },
    qqq: { name: "QQQ (Tech Focused)", grossReturn: 0.13, expenseRatio: 0.0020 },
};

const MAX_YEARS_TO_PROJECT = 70; // Safety limit for time to FIRE calculation

// --- HELPER: Calculate Target Nest Egg (Now takes SWR) ---
function calculateTargetNestEggInternal(parsedLifestyleToday, investmentHorizonYears, inflationRateForCalc, safeWithdrawalRateDecimal) {
    const futureMonthlyLifestyleCost = parsedLifestyleToday * Math.pow(1 + inflationRateForCalc, investmentHorizonYears);
    const annualExpensesAtRetirement = futureMonthlyLifestyleCost * 12;
    if (safeWithdrawalRateDecimal <= 0 || !isFinite(safeWithdrawalRateDecimal)) { 
        return { targetNestEgg: Infinity, futureMonthlyLifestyleCost };
    }
    const calculatedNestEgg = annualExpensesAtRetirement / safeWithdrawalRateDecimal;
    return {
        targetNestEgg: isFinite(calculatedNestEgg) ? calculatedNestEgg : Infinity,
        futureMonthlyLifestyleCost: futureMonthlyLifestyleCost
    };
}


// --- MODE 1: Calculate Monthly Savings Needed (Existing Logic Refactored) ---
export function calculateMonthlySavingsNeeded(inputs) {
    const { 
        currentAge, retirementAge, desiredMonthlyLifestyleToday,
        assumedInflationRate, initialInvestment = 0, instrument,
        safeWithdrawalRate // New input (e.g., 4 for 4%)
    } = inputs;

    // --- Input Parsing and Validation ---
    const parsedCurrentAge = parseInt(currentAge);
    const parsedRetirementAge = parseInt(retirementAge);
    const parsedLifestyleToday = parseFloat(desiredMonthlyLifestyleToday);
    const parsedInitialInvestment = parseFloat(initialInvestment);
    const directInflationInput = parseFloat(assumedInflationRate); 
    const inflationRateForCalc = directInflationInput / 100;
    const parsedSWR = parseFloat(safeWithdrawalRate);
    const swrDecimal = parsedSWR / 100;

    if (isNaN(parsedCurrentAge) || parsedCurrentAge < 18 || parsedCurrentAge > 99) throw new Error("Invalid current age.");
    if (isNaN(parsedRetirementAge) || parsedRetirementAge > 100 || parsedRetirementAge <= parsedCurrentAge) throw new Error("Invalid retirement age.");
    if (isNaN(parsedLifestyleToday) || parsedLifestyleToday <= 0) throw new Error("Desired monthly lifestyle must be positive.");
    if (isNaN(directInflationInput) || directInflationInput < 0 || directInflationInput > 20) throw new Error("Inflation rate must be 0-20%.");
    if (isNaN(parsedInitialInvestment) || parsedInitialInvestment < 0) throw new Error("Initial investment must be non-negative.");
    if (isNaN(parsedSWR) || parsedSWR <= 0 || parsedSWR > 15) throw new Error("SWR must be positive, typically 1-15%.");
    if (!instrument || !INSTRUMENT_DETAILS[instrument.toLowerCase()]) throw new Error("Invalid investment instrument.");
    // --- End Input Validation ---

    const investmentHorizonYears = parsedRetirementAge - parsedCurrentAge;
    const investmentHorizonMonths = investmentHorizonYears * 12;

    const { targetNestEgg, futureMonthlyLifestyleCost } = calculateTargetNestEggInternal(parsedLifestyleToday, investmentHorizonYears, inflationRateForCalc, swrDecimal);

    const instrumentDetail = INSTRUMENT_DETAILS[instrument.toLowerCase()];
    const netAnnualReturn = instrumentDetail.grossReturn - instrumentDetail.expenseRatio;
    const monthlyReturnRate = Math.pow(1 + netAnnualReturn, 1 / 12) - 1;

    let futureValueOfInitialInvestment = 0;
    if (parsedInitialInvestment > 0 && monthlyReturnRate !== -1) {
         futureValueOfInitialInvestment = parsedInitialInvestment * Math.pow(1 + monthlyReturnRate, investmentHorizonMonths);
    }

    const remainingTarget = targetNestEgg - futureValueOfInitialInvestment;
    let monthlyInvestmentNeeded;
    
    if (remainingTarget <= 0) { monthlyInvestmentNeeded = 0; }
    else if (!isFinite(targetNestEgg)) { monthlyInvestmentNeeded = Infinity; } // If target is infinity, needed is infinity
    else if (investmentHorizonMonths <= 0) { monthlyInvestmentNeeded = remainingTarget > 0 ? Infinity : 0; }
    else if (monthlyReturnRate === 0) { monthlyInvestmentNeeded = remainingTarget / investmentHorizonMonths; }
    else { 
        const numerator = remainingTarget * monthlyReturnRate; 
        const denominator = Math.pow(1 + monthlyReturnRate, investmentHorizonMonths) - 1; 
        monthlyInvestmentNeeded = (denominator === 0) ? (remainingTarget / investmentHorizonMonths) : (numerator / denominator); 
    }
    if (monthlyInvestmentNeeded < 0 || (!isFinite(monthlyInvestmentNeeded) && remainingTarget > 0) ) { 
        monthlyInvestmentNeeded = (remainingTarget > 0 && isFinite(remainingTarget) && investmentHorizonMonths > 0) ? Infinity : 0;
    }

    const projectionChartData = [];
    let currentBalance = parsedInitialInvestment;
    let totalMonthlyCapitalInvested = 0;
    if (isFinite(monthlyInvestmentNeeded) && isFinite(targetNestEgg)) {
        for (let year = 0; year < investmentHorizonYears; year++) {
            let yearlyMonthlyCapitalInvested = 0;
            for (let month = 0; month < 12; month++) {
                if (monthlyInvestmentNeeded > 0) { currentBalance += monthlyInvestmentNeeded; yearlyMonthlyCapitalInvested += monthlyInvestmentNeeded;}
                currentBalance *= (1 + monthlyReturnRate);
            }
            totalMonthlyCapitalInvested += yearlyMonthlyCapitalInvested;
            const totalCapitalAtYearEnd = parsedInitialInvestment + totalMonthlyCapitalInvested;
            const capitalGains = currentBalance - totalCapitalAtYearEnd;
            projectionChartData.push({ age: parsedCurrentAge + year + 1, capitalInvested: parseFloat(totalCapitalAtYearEnd.toFixed(0)), capitalGains: parseFloat(Math.max(0, capitalGains.toFixed(0))), totalValue: parseFloat(currentBalance.toFixed(0)), });
        }
        if (projectionChartData.length > 0 && targetNestEgg > 0 && Math.abs(currentBalance - targetNestEgg) > 1) { 
            const lastPoint = projectionChartData[projectionChartData.length - 1]; 
            const diff = targetNestEgg - lastPoint.totalValue; 
            lastPoint.capitalGains = Math.max(0, lastPoint.capitalGains + diff); 
            lastPoint.totalValue = parseFloat(targetNestEgg.toFixed(0)); 
        }
    }

    return {
        targetNestEgg: parseFloat(targetNestEgg.toFixed(2)),
        monthlyInvestmentNeeded: parseFloat(monthlyInvestmentNeeded.toFixed(2)),
        projectionChartData,
        assumptions: { 
            mode: "calculateMonthlySavings", currentAge: parsedCurrentAge, retirementAge: parsedRetirementAge,
            initialInvestment: parsedInitialInvestment, desiredMonthlyLifestyleToday: parsedLifestyleToday,
            futureMonthlyLifestyleCost: parseFloat(futureMonthlyLifestyleCost.toFixed(2)),
            assumedInflationRatePercent: directInflationInput, 
            investmentHorizonYears: investmentHorizonYears, instrumentName: instrumentDetail.name, 
            grossAnnualReturn: `${(instrumentDetail.grossReturn * 100).toFixed(1)}%`,
            expenseRatio: `${(instrumentDetail.expenseRatio * 100).toFixed(4)}%`,
            netAnnualReturn: `${(netAnnualReturn * 100).toFixed(2)}%`,
            safeWithdrawalRatePercent: parsedSWR,
        }
    };
}


// --- MODE 2: Calculate Time to FIRE ---
export function calculateTimeToFIRE(inputs) {
    const { 
        currentAge, desiredMonthlyLifestyleToday, assumedInflationRate, 
        initialInvestment = 0, plannedMonthlyInvestment, instrument,
        safeWithdrawalRate 
    } = inputs;

    const parsedCurrentAge = parseInt(currentAge);
    const parsedLifestyleToday = parseFloat(desiredMonthlyLifestyleToday);
    const directInflationInput = parseFloat(assumedInflationRate);
    const inflationRateForCalc = directInflationInput / 100;
    const parsedInitialInvestment = parseFloat(initialInvestment);
    const parsedPlannedMonthlyInvestment = parseFloat(plannedMonthlyInvestment);
    const parsedSWR = parseFloat(safeWithdrawalRate);
    const swrDecimal = parsedSWR / 100;

    // Validations
    if (isNaN(parsedCurrentAge) || parsedCurrentAge < 18 || parsedCurrentAge > 99) throw new Error("Invalid current age.");
    if (isNaN(parsedLifestyleToday) || parsedLifestyleToday <= 0) throw new Error("Desired monthly lifestyle must be positive.");
    if (isNaN(directInflationInput) || directInflationInput < 0 || directInflationInput > 20) throw new Error("Inflation rate must be 0-20%.");
    if (isNaN(parsedInitialInvestment) || parsedInitialInvestment < 0) throw new Error("Initial investment must be non-negative.");
    if (isNaN(parsedPlannedMonthlyInvestment) || parsedPlannedMonthlyInvestment <= 0) throw new Error("Planned monthly investment must be positive.");
    if (isNaN(parsedSWR) || parsedSWR <= 0 || parsedSWR > 15) throw new Error("SWR must be positive, typically 1-15%.");
    if (!instrument || !INSTRUMENT_DETAILS[instrument.toLowerCase()]) throw new Error("Invalid investment instrument.");


    const instrumentDetail = INSTRUMENT_DETAILS[instrument.toLowerCase()];
    const netAnnualReturn = instrumentDetail.grossReturn - instrumentDetail.expenseRatio;
    const monthlyReturnRate = Math.pow(1 + netAnnualReturn, 1 / 12) - 1;

    let currentBalance = parsedInitialInvestment;
    let yearsToFIRE = 0;
    let ageAtFIRE = parsedCurrentAge;
    let totalMonthlyCapitalInvested = 0;
    const projectionChartData = [];
    let finalTargetNestEgg = 0;
    let futureMonthlyCostAtFIRE = 0;

    if (monthlyReturnRate <= -1) { // If returns are -100% or worse, FIRE is impossible with savings
        return {
            targetNestEgg: Infinity, ageAtFIRE: Infinity, yearsToFIRE: Infinity, projectionChartData: [],
            assumptions: { /* ... basic assumptions ... */ mode: "calculateTimeToFIRE", currentAge: parsedCurrentAge, plannedMonthlyInvestment: parsedPlannedMonthlyInvestment, initialInvestment: parsedInitialInvestment, desiredMonthlyLifestyleToday: parsedLifestyleToday, futureMonthlyLifestyleCost: Infinity, assumedInflationRatePercent: directInflationInput, investmentHorizonYears: MAX_YEARS_TO_PROJECT, instrumentName: instrumentDetail.name, grossAnnualReturn: `${(instrumentDetail.grossReturn * 100).toFixed(1)}%`, expenseRatio: `${(instrumentDetail.expenseRatio * 100).toFixed(4)}%`, netAnnualReturn: `${(netAnnualReturn * 100).toFixed(2)}%`, safeWithdrawalRatePercent: parsedSWR, }
        };
    }


    for (let year = 0; year < MAX_YEARS_TO_PROJECT; year++) {
        yearsToFIRE = year + 1;
        ageAtFIRE = parsedCurrentAge + yearsToFIRE;

        const { targetNestEgg: yearlyTargetNestEgg, futureMonthlyLifestyleCost } = calculateTargetNestEggInternal(parsedLifestyleToday, yearsToFIRE, inflationRateForCalc, swrDecimal);
        futureMonthlyCostAtFIRE = futureMonthlyLifestyleCost;
        finalTargetNestEgg = yearlyTargetNestEgg;

        let yearlyCapitalInvested = 0;
        for (let month = 0; month < 12; month++) { 
            currentBalance += parsedPlannedMonthlyInvestment; 
            yearlyCapitalInvested += parsedPlannedMonthlyInvestment; 
            currentBalance *= (1 + monthlyReturnRate); 
        }
        totalMonthlyCapitalInvested += yearlyCapitalInvested;
        const totalCapitalAtYearEnd = parsedInitialInvestment + totalMonthlyCapitalInvested;
        const capitalGains = currentBalance - totalCapitalAtYearEnd;
        projectionChartData.push({ age: parsedCurrentAge + year + 1, capitalInvested: parseFloat(totalCapitalAtYearEnd.toFixed(0)), capitalGains: parseFloat(Math.max(0, capitalGains.toFixed(0))), totalValue: parseFloat(currentBalance.toFixed(0)), });

        if (currentBalance >= yearlyTargetNestEgg && isFinite(yearlyTargetNestEgg)) {
            break; 
        }
        if (year === MAX_YEARS_TO_PROJECT - 1 || !isFinite(yearlyTargetNestEgg)) {
            return {
                targetNestEgg: parseFloat(finalTargetNestEgg.toFixed(2)), ageAtFIRE: Infinity, yearsToFIRE: Infinity, projectionChartData,
                assumptions: { mode: "calculateTimeToFIRE", currentAge: parsedCurrentAge, plannedMonthlyInvestment: parsedPlannedMonthlyInvestment, initialInvestment: parsedInitialInvestment, desiredMonthlyLifestyleToday: parsedLifestyleToday, futureMonthlyLifestyleCost: parseFloat(futureMonthlyCostAtFIRE.toFixed(2)), assumedInflationRatePercent: directInflationInput, investmentHorizonYears: MAX_YEARS_TO_PROJECT, instrumentName: instrumentDetail.name, grossAnnualReturn: `${(instrumentDetail.grossReturn * 100).toFixed(1)}%`, expenseRatio: `${(instrumentDetail.expenseRatio * 100).toFixed(4)}%`, netAnnualReturn: `${(netAnnualReturn * 100).toFixed(2)}%`, safeWithdrawalRatePercent: parsedSWR, }
            };
        }
    }
    
    if (projectionChartData.length > 0 && finalTargetNestEgg > 0 && isFinite(finalTargetNestEgg) && currentBalance >= finalTargetNestEgg) { 
        const lastPoint = projectionChartData[projectionChartData.length - 1]; 
        const diff = finalTargetNestEgg - lastPoint.totalValue; 
        if (Math.abs(diff) > 1) { 
            lastPoint.capitalGains = Math.max(0, lastPoint.capitalGains + diff); 
            lastPoint.totalValue = parseFloat(finalTargetNestEgg.toFixed(0)); 
        } else { 
            lastPoint.totalValue = parseFloat(finalTargetNestEgg.toFixed(0)); 
        } 
    }

    return {
        targetNestEgg: parseFloat(finalTargetNestEgg.toFixed(2)),
        ageAtFIRE: ageAtFIRE,
        yearsToFIRE: yearsToFIRE,
        projectionChartData,
        assumptions: { 
            mode: "calculateTimeToFIRE", currentAge: parsedCurrentAge, plannedMonthlyInvestment: parsedPlannedMonthlyInvestment,
            initialInvestment: parsedInitialInvestment, desiredMonthlyLifestyleToday: parsedLifestyleToday,
            futureMonthlyLifestyleCost: parseFloat(futureMonthlyCostAtFIRE.toFixed(2)),
            assumedInflationRatePercent: directInflationInput, 
            investmentHorizonYears: yearsToFIRE, instrumentName: instrumentDetail.name, 
            grossAnnualReturn: `${(instrumentDetail.grossReturn * 100).toFixed(1)}%`,
            expenseRatio: `${(instrumentDetail.expenseRatio * 100).toFixed(4)}%`,
            netAnnualReturn: `${(netAnnualReturn * 100).toFixed(2)}%`,
            safeWithdrawalRatePercent: parsedSWR,
        }
    };
}